import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'
import { Link } from 'react-router-dom'
import { useSetRecoilState } from 'recoil'

import { Link as LinkIcon } from '@mui/icons-material'
import InstagramIcon from '@mui/icons-material/Instagram'
import { Box, Button, Card, Chip, Container, Grid, Tooltip, Typography, useMediaQuery, useTheme } from '@mui/material'

import { formatDate } from 'src/utils/formatDateTime'
import { formatUnivEnteredYearLimit } from 'src/utils/formatUnivEnteredYearLimit'

import LikeBtn from 'src/components/LikeBtn'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import AnswerModal from 'src/content/club/Intro/AnswerModal'
import ClubInfoGrid from 'src/content/club/Intro/ClubInfoGrid'

import { useGetApplication } from 'src/hooks/api/apply'
import { useDeleteLikeClub, useGetClubInformation, useGetRecruitInformation, usePostLikeClub } from 'src/hooks/api/club'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { isLikeBtnClickedAtom } from 'src/recoil/atom'
import getDateOrNull from 'src/utils/getDateOrNull'

const today = new Date()

const Intro: React.FC = () => {
  const { id, recruitId } = useParams()
  const clubId = Number(id)
  const recruitIdNum = Number(recruitId)
  const navigate = useNavigate()

  const theme = useTheme()
  const isDesktopOrTablet = useMediaQuery(theme.breakpoints.up('sm'))

  const [isLike, setIsLike] = useState(false)
  const [isNotDummy, setIsNotDummy] = useState(false)
  const [isOpenForm, setIsOpenForm] = useState(false)
  const setIsLikeBtnClicked = useSetRecoilState(isLikeBtnClickedAtom)
  const [univEnteredYearLimt, setunivEnteredYearLimt] = useState<string>('')
  const { userInfo } = useSafeAuthUser()

  const postLikeClubMutation = usePostLikeClub(clubId)
  const deleteLikeClubMutation = useDeleteLikeClub(clubId)

  const userClub = userInfo?.clubs.find(c => c.id === clubId)
  const userAdmin = userInfo?.clubs.find(c => c.id === clubId && (c.permission === 'SUPERADMIN' || 'ADMIN'))
  const isMember = !!userClub
  const isAdmin = !!userAdmin
  const isConnected = isMember && userClub?.isConnected
  const { data: club, isFetched } = useGetClubInformation(clubId)
  const { data: recruitInfo } = useGetRecruitInformation(clubId, isMember, today, recruitIdNum)
  const { data: application, isSuccess: isAlreadyApplied } = useGetApplication(clubId, recruitInfo?.id)

  const startAt = recruitInfo ? new Date(recruitInfo.startAt) : today
  const endAt = recruitInfo ? getDateOrNull(recruitInfo.endAt) : today

  const isBeforeDeadline = !!recruitInfo && (!endAt || today <= endAt)
  const isRecruitingNow = isBeforeDeadline && today >= startAt

  useEffect(() => {
    if (isFetched && club) {
      setIsLike(club?.isliked)
      setIsNotDummy(club?.hasSuperadmin)
    }
  }, [isFetched])

  useEffect(() => {
    if (recruitInfo) {
      setunivEnteredYearLimt(formatUnivEnteredYearLimit(recruitInfo))
    } else setunivEnteredYearLimt('무관')
  }, [recruitInfo])

  const handleLike = async () => {
    try {
      {
        isLike ? await deleteLikeClubMutation.mutateAsync() : await postLikeClubMutation.mutateAsync()
      }
      setIsLike(!isLike)
      setIsLikeBtnClicked(true)
    } catch (error) {}
  }

  const handleOpenFormModal: React.MouseEventHandler<HTMLButtonElement> = () => {
    setIsOpenForm(isOpenForm => !isOpenForm)
  }

  const handleNavigateApply: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (recruitId) navigate(`/club/${clubId}/apply/${recruitInfo?.id}`)
    else navigate(`apply/${recruitInfo?.id}`)
  }

  return (
    <>
      <Helmet>
        <title>{club ? club.name : '404 PAGE'}</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container>
          {club && (
            <Grid item xs={12} md={7}>
              <Box
                display="flex"
                flexDirection={isDesktopOrTablet ? 'row' : 'column'}
                alignItems={isDesktopOrTablet ? 'center' : ''}
              >
                <Box display={'flex'} alignItems={'baseline'}>
                  <Box display="flex" flexDirection="row" alignItems="center" marginRight="10px" mt={2}>
                    <Typography variant="h3">{club.name}</Typography>
                    {club?.instagramId && <InstagramConnect instagramId={club.instagramId} />}
                    <LikeBtn handleLike={handleLike} isLike={isLike} />
                  </Box>
                  {!isNotDummy && (
                    <Tooltip title={'이 동아리는 현재 운영진으로 등록된 회원이 없어요!'} placement="top-end">
                      <Link to="/club/register" target="_blank" style={{ textDecoration: 'none' }}>
                        <Box display={'flex'} alignContent={'center'} gap={1}>
                          <Typography variant="body1" fontWeight={'bold'} color="primary">
                            "{club.name}"의 대표자로 등록하기
                          </Typography>
                          <LinkIcon fontSize="small" color="primary" />
                        </Box>
                      </Link>
                    </Tooltip>
                  )}
                </Box>
              </Box>
              <Typography variant="subtitle2" gutterBottom>
                {club.description}
              </Typography>
              <Box>
                {club.belonging && <ClubChip label={club.belonging} isDesktopOrTablet={isDesktopOrTablet} />}
                {club.category && <ClubChip label={club.category} isDesktopOrTablet={isDesktopOrTablet} />}
                {club.location && <ClubChip label={club.location} isDesktopOrTablet={isDesktopOrTablet} />}
                {!isDesktopOrTablet && (
                  <ClubChip
                    label={isRecruitingNow ? '모집중' : '모집종료'}
                    isDesktopOrTablet={isDesktopOrTablet}
                    isRecruitingNow={isRecruitingNow}
                  />
                )}
              </Box>
            </Grid>
          )}
          <Grid item container xs={12} md={5} mt={1} display="flex" flexDirection="column" justifyContent="center">
            <Grid item container display="flex" flexDirection="row">
              <ClubInfoGrid label="모집 시작" value={recruitInfo ? formatDate(startAt) : '-'} />
              <ClubInfoGrid label="모집 종료" value={recruitInfo ? (endAt ? formatDate(endAt) : '상시 모집') : '-'} />
              <ClubInfoGrid label="예상 선발 인원" value={recruitInfo?.limit ? `${recruitInfo.limit}명` : '무관'} />
              <ClubInfoGrid label="소속 제한" value={recruitInfo?.allowedCollege ?? '무관'} />
              <ClubInfoGrid label="학번 제한" value={univEnteredYearLimt} />
            </Grid>
            <Grid item display="flex" justifyContent="center">
              {isAdmin ? (
                <>
                  <Button
                    component={Link}
                    to={`/club/${clubId}/profile`}
                    variant="contained"
                    sx={{ width: '100%', height: '50%', padding: theme.spacing(0.5), marginY: 1, marginRight: '2%' }}
                  >
                    내 활동
                  </Button>
                  <Button
                    component={Link}
                    to={`/manage/${clubId}/member`}
                    variant="contained"
                    sx={{ width: '100%', height: '50%', padding: theme.spacing(0.5), marginY: 1, marginLeft: '2%' }}
                    color="success"
                  >
                    동아리 관리
                  </Button>
                </>
              ) : isMember ? (
                <Button
                  component={Link}
                  to={`/club/${clubId}/profile`}
                  variant="contained"
                  sx={{ width: '100%', padding: theme.spacing(0.5), marginY: 1 }}
                >
                  내 활동
                </Button>
              ) : (
                isDesktopOrTablet && <RecruitingStatusChip isRecruitingNow={isRecruitingNow} />
              )}
            </Grid>
          </Grid>
        </Grid>
      </PageTitleWrapper>
      <Container maxWidth="md">
        {recruitInfo && recruitInfo.contents ? (
          <Card>
            {recruitInfo.title && (
              <Typography sx={{ p: 2, fontSize: '20px', fontWeight: 'bold', textAlign: 'center' }} overflow="auto">
                {recruitInfo.title}
              </Typography>
            )}
            <Box p={2} overflow="auto">
              <div className="view ql-editor" dangerouslySetInnerHTML={{ __html: recruitInfo.contents }}></div>
            </Box>
          </Card>
        ) : (
          <Card>
            <Box height="50vh" display="flex" justifyContent="center" alignItems="center">
              <Typography variant="h4">동아리에서 올린 소개글이 없어요 ㅠ.ㅠ</Typography>
            </Box>
          </Card>
        )}
        <Box textAlign="center" my={2}>
          {isMember ? (
            <Button
              variant="contained"
              disabled={isConnected}
              sx={{ paddingX: 5 }}
              onClick={() => navigate('agreement')}
            >
              내 정보 제출하기
            </Button>
          ) : isAlreadyApplied ? (
            <Button variant="contained" sx={{ paddingX: 5 }} onClick={handleOpenFormModal}>
              지원서 확인하기
            </Button>
          ) : (
            <Button
              variant="contained"
              disabled={!isBeforeDeadline || isMember}
              sx={{ paddingX: 5 }}
              onClick={handleNavigateApply}
            >
              {isRecruitingNow ? '지원서 작성' : '지원서 문항 보기'}
            </Button>
          )}
        </Box>
        {application && <AnswerModal open={isOpenForm} onClose={handleOpenFormModal} application={application} />}
      </Container>
    </>
  )
}

export default Intro

const InstagramConnect: React.FC<{ instagramId: string }> = ({ instagramId }) => {
  const handleClickInstagram = () => window.open(`https://instagram.com/${instagramId}`, '_blank')

  return (
    <Button onClick={handleClickInstagram}>
      <InstagramIconColored />
    </Button>
  )
}

const InstagramIconColored = () => (
  <>
    <svg width={0} height={0}>
      <linearGradient id="linearColors" x1={1} y1={0} x2={1} y2={1}>
        <stop offset={0} stopColor="#4f5bd5" />
        <stop offset={0.1} stopColor="#962fbf" />
        <stop offset={0.4} stopColor="#d62976" />
        <stop offset={0.6} stopColor="#fa7e1e" />
        <stop offset={0.9} stopColor="#feda75" />
      </linearGradient>
    </svg>
    <InstagramIcon sx={{ fill: 'url(#linearColors)' }} />
  </>
)

const RecruitingStatusChip = ({ isRecruitingNow }: { isRecruitingNow: boolean }) => {
  return (
    <Chip
      label={isRecruitingNow ? '모집 중' : '모집 기간이 아니에요'}
      sx={{ width: '100%', padding: 0.5, marginY: 1 }}
      color={isRecruitingNow ? 'success' : 'warning'}
    />
  )
}

const ClubChip = ({
  label,
  isDesktopOrTablet,
  isRecruitingNow
}: {
  label: string
  isDesktopOrTablet: boolean
  isRecruitingNow?: boolean
}) => {
  return (
    <Chip
      label={label}
      size="small"
      sx={{ padding: 0.5, fontSize: isDesktopOrTablet ? 13 : 11, marginRight: 1 }}
      color={isRecruitingNow !== undefined ? (isRecruitingNow ? 'success' : 'warning') : 'default'}
    />
  )
}
